import React from "react"
import { useSelector } from "react-redux"
import Results from "../partials/modals/Results"
import Header from "../partials/Header"
import FormOne from "../partials/FormOne"
import FormTwo from "../partials/FormTwo"
import SongMayExist from "../partials/modals/SongMayExist"
import SongExist from "../partials/modals/SongExist"
import Footer from "../partials/Footer"
import "../../scss/pages/Add.scss"

const Add = ({ location }) => {

    const songMayExist = useSelector(state => state.formData.songMayExist)
    const songExist = useSelector(state => state.formData.songExist)
    const currentForm = useSelector(state => state.formData.currentForm)
    
    return (
        <div className="add">
            {
                location.pathname.includes("/search/") ?
                <Results /> :
                ""
            }
            <Header />
            <main className={ (location.pathname.includes("/search/") || songExist || songMayExist) ? "fixed" : "" }>
                <div className="sub-container">
                    <h1>Add new song<br/>to the Eimilyrics collection</h1>
                    {
                        currentForm === 1 ?
                        <FormOne /> :
                        <FormTwo />
                    }
                </div>
            </main>
            {
                songMayExist ?
                <SongMayExist /> :
                ""
            }
            {
                songExist ?
                <SongExist /> :
                ""
            }
            {
                (!location.pathname.includes("/search/") || !songExist || !songMayExist) ?
                <Footer /> :
                ""
            }
        </div>
    )

}

export default Add