import axios from "axios"

export const writerAutocomplete = () => {

    return async (dispatch, getState) => {

        const writer = getState().formData.formOne.writer

        if (!writer || writer.length < 4){
            return dispatch({
                type: "EMPTY_WRITERS_LIST"
            })
        }
        
        try {
            let writers = await axios.get(`/api/writer-autocomplete/${ writer }`)
            writers = writers.data

            if (!writers){
                return dispatch({
                    type: "EMPTY_WRITERS_LIST"
                })
            }
            
            dispatch({
                type: "UPDATE_WRITERS",
                writers
            })
        }
        catch {
            dispatch({
                type: "EMPTY_WRITERS_LIST"
            })
        }
    
    }

}