import { validateFormOneData } from "../functions/validateFormOneData"
import axios from "axios"

export const checkSongExist = () => {
    return async (dispatch, getState) => {

        dispatch({ type: "FORM_ONE_SUBMITTED" })

        const formOne = getState().formData.formOne
        const { title, writer, dialect, strict } = formOne
        
        // validate data
        const validation = validateFormOneData({ title, writer, dialect })
        if (validation.status !== 200){
            return dispatch({
                type: "FORM_ONE_ERROR",
                error: validation.error
            })
        }

        // send post request to check if song already exist
        try {
            const check = await axios.post("/api/check", { title, writer, dialect, strict })
            if (check.data !== "ok"){
                return dispatch({
                    type: "FORM_ONE_ERROR",
                    error: {
                        code: "unexpected",
                        message: "An unexpected error occured, please try again later!"
                    }
                })
            }

            return dispatch({
                type: "SONG_NOT_EXIST"
            })
        }
        catch (err){
            const response = err.response
            if (response){
                if (response.data.code === "song-already-exist"){
                    if (response.data.strict){
                        return dispatch({
                            type: "SONG_MAY_EXIST",
                            song: response.data.song
                        })
                    }
                    else {
                        return dispatch({
                            type: "SONG_EXIST",
                            song: response.data.song
                        })
                    }
                }
                else {
                    return dispatch({
                        type: "FORM_ONE_ERROR",
                        error: response.data
                    })
                }
            }
            else {
                return dispatch({
                    type: "FORM_ONE_ERROR",
                    error: {
                        code: "unexpected",
                        message: "An unexpected error occured, please try again later!"
                    }
                })
            }
        }

    }
}