import React, { useState, useEffect } from "react"
import { useSelector, useDispatch } from "react-redux"
import { Link } from "react-router-dom"
import { checkSongExist } from "../../../actions/checkSongExist"
import "../../../scss/partials/modals/SongMayExist.scss"

const Confirm = () => {

    const dispatch = useDispatch()
    const songMayExist = useSelector(state => state.formData.songMayExist)
    const exit = () => {
        dispatch({
            type: "EXIT_SONG_MAY_EXIST_MODAL"
        })
    }
    const yes = () => {
        dispatch({
            type: "SONG_EXIST",
            song: songMayExist
        })
    }
    const no = () => {
        dispatch({
            type: "DISABLE_STRICT_MODE"
        })
        dispatch(checkSongExist())
    }
    const [ visible, toggleVisibility ] = useState(false)
    useEffect(() => {
        setTimeout(() => toggleVisibility(true), 50)
    }, [])
    
    return (
        <div className="song-may-exist">
            <div className="bg-layer" onClick={ exit }></div>
            <section className={ visible ? "visible" : "" }>
                <div className="sub-container">
                    <Link to={ `/song/${ songMayExist._id }` } className="header" style={ songMayExist.youtube ? { backgroundImage: `url(https://img.youtube.com/vi/${ songMayExist.youtube }/default.jpg)` } : {} }>
                        <span className={ songMayExist.youtube ? "with-thumbnail" : "" }>Is this the song <br/>you're trying to add?</span>
                    </Link>
                    <div className="song">
                        <span className="title"><b>Title:</b> { songMayExist.title }</span>
                        {
                            songMayExist.writer ?
                            <span className="writer"><b>Writer:</b> { songMayExist.writer }</span> :
                            <span className="writer"><b>Writer:</b> Unknown <Link to={ `/edit/${ songMayExist._id }` }>I know the writer</Link></span>
                        }
                        <span className="dialect"><b>Dialect:</b> { songMayExist.dialect }</span>
                        <div className="lyrics">
                            <b>Lyrics:</b>
                            {
                                songMayExist.lyrics.split("\\n").map((line, i) => <p key={ i }>{ line }</p>)
                            }
                        </div>
                    </div>
                </div>
                <div className="options">
                    <button type="button" onClick={ yes }>Yes</button>
                    <button type="button" onClick={ no }>No</button>
                </div>
            </section>
        </div>
    )

}

export default Confirm