import React, { useEffect, useRef } from "react"
import { useSelector, useDispatch } from "react-redux"
import { writerAutocomplete } from "../../actions/writerAutocomplete"
import { checkSongExist } from "../../actions/checkSongExist"
import DropDownArrow from "../partials/icons/DropDownArrow"
import Loading from "../../icons/loading.gif"

const FormOne = () => {

    const titleInput = useRef()
    const writerInput = useRef()
    const dialectInput = useRef()
    const dispatch = useDispatch()
    const formOne = useSelector(state => state.formData.formOne)
    const { activated, title, writer, writers, dialect, error, strict } = formOne

    const inputChanged = e => {
        const field = e.target.name
        const value = e.target.value
        
        dispatch({
            type: "FORM_ONE_INPUT_CHANGED",
            field,
            value
        })

        if (field === "writer"){
            dispatch(writerAutocomplete())
        }
    }
    
    const submitForm = e => {
        e.preventDefault()

        if (!activated){
            return null
        }

        dispatch(checkSongExist())
    }
    
    useEffect(() => {
        if (error && error.code === "title"){
            titleInput.current.focus()
        }
        else if (error && error.code === "writer"){
            writerInput.current.focus()
        }
        else if (error && error.code === "dialect"){
            dialectInput.current.focus()
        }
    }, [error])

    useEffect(() => {
        dispatch({
            type: "ENABLE_STRICT_MODE"
        })
    }, [dispatch, strict])

    return (
        <form className="form1" onSubmit={ submitForm }>
            <label htmlFor="title">Song title *</label>
            <input type="text" id="title" autoComplete="off" name="title" value={ title } onChange={ inputChanged } ref={ titleInput }/>
            {
                (error && error.code === "title") ?
                <p className="message">{ error.message }</p> :
                ""
            }
            <label htmlFor="writer">Writer <span>( Just leave it blank if you're not sure )</span></label>
            <input type="text" id="writer" name="writer" value={ writer } onChange={ inputChanged } ref={ writerInput } autoComplete="off" list="writers"/>
            <datalist id="writers">
                {
                    (writers.length && writer) ?
                    writers.map((w, i) => {
                        return writer !== w._id ?
                        <option value={ w._id } key={ i }>{ w._id }</option> :
                        ""
                    }) :
                    ""
                }
            </datalist>
            {
                (error && error.code === "writer") ?
                <p className="message">{ error.message }</p> :
                ""
            }
            <label htmlFor="dialect">Specify dialect</label>
            <div className="options">
                <DropDownArrow />
                <select id="dialect" name="dialect" value={ dialect } onChange={ inputChanged } ref={ dialectInput }>
                    <option value="Paite">Paite</option>
                    <option value="Tedim Chin">Tedim Chin</option>
                    <option value="Thadou Kuki">Thadou Kuki</option>
                    <option value="Vaiphei">Vaiphei</option>
                    <option value="Zou">Zou</option>
                    <option value="Gangte">Gangte</option>
                    <option value="Simte">Simte</option>
                    <option value="Hmar">Hmar</option>
                    <option value="Mizo">Mizo</option>
                </select>
            </div>
            {
                (error && error.code === "dialect") ?
                <p className="message">{ error.message }</p> :
                ""
            }
            <button type={ activated ? "submit" : "button" }>
                {
                    activated ?
                    "Next" :
                    <img src={ Loading } alt="loading icon"/>
                }
            </button>
            {
                (error && error.code === "unexpected") ?
                <p className="message unexpected">{ error.message }</p> :
                ""
            }
        </form>
    )

}

export default FormOne