import React, { useState } from "react"
import { useSelector, useDispatch } from "react-redux"
import LeftArrow from "./icons/LeftArrow"

const FormTwo = () => {

    const [ thumbnail, updateThumbnail ] = useState(null)
    const formTwo = useSelector(state => state.formData.formTwo)
    const { error } = formTwo
    const dispatch = useDispatch()
    const switchToFormOne = () => {
        dispatch({
            type: "SWITCH_TO_FORM_ONE"
        })
    }

    return (
        <form className="form2">
            <span className="prev" tabIndex="0" onClick={ switchToFormOne }><LeftArrow />Prev</span><br/>
            <label htmlFor="youtube">Youtube video link <span>( Optional )</span></label>
            <div className={ thumbnail ? "thumbnail visible" : "thumbnail" }>
                <div style={ thumbnail ? { backgroundImage: `url(https://img.youtube.com/vi/${ thumbnail.id }/default.jpg)` } : {} }></div>
            </div>
            <input type="text" className={ thumbnail ? "with-thumbnail" : "" } id="youtube" name="youtube" autoComplete="off"/>
            {
                (error && error.type === "youtube") ?
                <p className="message">{ error.message }</p> :
                ""
            }
            <label htmlFor="lyrics">Lyrics * <span>( Format: Verse &gt; Chorus &gt; Verse &gt; Chorus &gt; ... )</span></label>
            <p contentEditable={ true } id="lyrics"></p>
            {
                (error && error.type === "lyrics") ?
                <p className="message">{ error.message }</p> :
                ""
            }
            <button type="submit">Add song</button>
            {
                (error && error.type === "unexpected") ?
                <div className="message">{ error.message }</div> :
                ""
            }
        </form>
    )

}

export default FormTwo