import React, { useState, useEffect, useRef } from "react"
import { withRouter } from "react-router-dom"
import SearchIcon from "./icons/SearchIcon"

import { songs } from "../../songs"

const Search = ({ history }) => {

    const [ searchInput, updateSearchInput ] = useState("")
    const [ suggestions, updateSuggestions ] = useState([])
    const searchInputElement = useRef()

    const searchForSuggestions = query => {
        query = query.split(" ").join("").toLowerCase()

        setTimeout(() => {
            let newSuggestions = []
            for (let i = 0; i < 8; i++){
                if (newSuggestions.length > 5){
                    break
                }

                const title = songs[i].title.split(" ").join("").toLowerCase()

                if (title.includes(query)){
                    newSuggestions = [...newSuggestions, songs[i].title]
                }
            }

            updateSuggestions(newSuggestions)
        }, 500)
    }

    useEffect(() => {
        if (history.location.pathname === "/search"){
            searchInputElement.current.focus()
        }
        else {
            searchInputElement.current.blur()

            // fill search input value on component did mount
            if (history.location.pathname.includes("/search/")){
                const query = history.location.pathname.split("/search/")[1]
                if (query){
                    updateSearchInput(query)
                }
            }
        }
    }, [history.location.pathname])

    useEffect(() => {
        // run only when searchInput updated
        if (searchInput){
            searchForSuggestions(searchInput)
        }
        else {
            // empty suggestions list
            updateSuggestions([])
        }
    }, [searchInput])

    const submitForm = e => {
        e.preventDefault()

        // go to results page
        if (searchInput.length > 0){
            const pathname = history.location.pathname
            if (pathname === "/search" || pathname === "/"){
                history.push(`/search/${ searchInput }`)
            }
            else {
                history.push(`${ history.location.pathname }/search/${ searchInput }`)
            }
        }
    }

    const searchInputIsTouched = () => {
        const pathname = history.location.pathname
        if (pathname === "/"){
            history.push("/search")
        }
    }

    return (
        <form className="search" onSubmit={ submitForm }>
            <input type="text" placeholder="Find songs" autoComplete="off" list="suggestions" value={ searchInput } onChange={ e => updateSearchInput(e.target.value) } onTouchEnd={ searchInputIsTouched } ref={ searchInputElement }/>
            {
                (suggestions.length && searchInput) ?
                <datalist id="suggestions">
                    {
                        suggestions.map((suggestion, i) => {
                            return searchInput !== suggestion ?
                            <option value={ suggestion } key={ i }/> :
                            ""
                        })
                    }
                </datalist> :
                ""
            }
            <button type="submit" tabIndex="-1">
                <SearchIcon />
            </button>
        </form>
    )

}

export default withRouter(Search)