import React from "react"
import { Link } from "react-router-dom"

const Song = ({ song }) => {

    const songPath = `/${ song.writer.split(" ").join("-").toLowerCase() }/${ song.title.split(" ").join("-").toLowerCase() }`
    const writerPath = `/writer/${ song.writer.split(" ").join("-").toLowerCase() }`
    const dialectPath = `/dialect/${ song.dialect.split(" ").join("-").toLowerCase() }`
    
    return (
        <div className={ song.thumbnail ? "song with-thumbnail" : "song" }>
            <p className="head"><Link to={ dialectPath }>{ song.dialect }</Link>{ song.writer ? <span></span> : "" }{ song.writer ? <Link to={ writerPath }>{ song.writer }</Link> : "" }</p>
            <Link to={ songPath } className="title">{ song.title }</Link>
            <div className={ song.thumbnail ? "content with-thumbnail" : "content" }>
                {
                    song.thumbnail ?
                    <Link to={ songPath } className="thumbnail" style={{ backgroundImage: `url(https://img.youtube.com/vi/${ song.thumbnail }/default.jpg)` }}>
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 50 50">
                            <circle cx="25" cy="25" r="25" />
                            <polyline points="18,15 38,25 18,35 18,15" />
                        </svg>
                    </Link> :
                    ""
                }
                <p className="lyrics">{ song.lyrics }</p>
                <span className="ellipsis"></span>
            </div>
        </div>
    )

}

export default Song