import React, { useState, useEffect } from "react"
import { Link, NavLink } from "react-router-dom"
import Search from "../partials/Search"
import "../../scss/partials/Header.scss"
import Logo from "../../icons/logo.svg"

const Header = () => {

    const [ menuVisible, toggleMenu ] = useState(false)

    useEffect(() => {
        document.onscroll = () => toggleMenu(false)
        
        return () => document.onscroll = null
    }, [])

    return (
        <header>
            <section>
                <Link to="/" className="title"><img src={ Logo } alt="Eimilyrics logo" className="logo"/>E<span>IMILYRIC</span>S</Link>
                <Search />
                <div className="menu" tabIndex="0" onBlur={ () => toggleMenu(false) } onClick={ () => menuVisible ? toggleMenu(false) : toggleMenu(true) }>
                    <button type="button" tabIndex="-1" className={ menuVisible ? "hamburger menu-visible" : "hamburger" }>
                        <span></span>
                        <span></span>
                        <span></span>
                    </button>
                    <ul className={ menuVisible ? "visible" : "" }>
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" className="triangle">
                            <polyline points="20,0 20,20 0,20 20,0"/>
                        </svg>
                        <li><NavLink exact to="/" className="nav" onFocus={ () => toggleMenu(true) }>Home</NavLink></li>
                        <li><NavLink to="/add" className="nav" onFocus={ () => toggleMenu(true) }>Add new song</NavLink></li>
                        <li><NavLink to="/gospel" className="nav" onFocus={ () => toggleMenu(true) }>Gospel songs</NavLink></li>
                        <li><NavLink to="/romantic" className="nav" onFocus={ () => toggleMenu(true) }>Romantic songs</NavLink></li>
                        <li><NavLink to="/secular" className="nav" onFocus={ () => toggleMenu(true) }>Secular songs</NavLink></li>
                        <li><NavLink to="/terms-of-use" className="nav" onFocus={ () => toggleMenu(true) }>Terms of use</NavLink></li>
                        <li><NavLink to="/privacy-policy" className="nav" onFocus={ () => toggleMenu(true) }>Privacy policy</NavLink></li>
                        <li><NavLink to="/report-a-bug" className="nav" onFocus={ () => toggleMenu(true) }>Report a bug</NavLink></li>
                    </ul>
                </div>
            </section>
        </header>
    )

}

export default Header