import React from "react"
import Loading from "../../icons/loading.gif"

const Message = ({ type, message }) => {

    let icon
    if (type === "loading"){
        icon = Loading
    }

    return (
        <div className="message">
            <img src={ icon } alt={ message }/>
            <span>{ message }</span>
        </div>
    )

}

export default Message