import React from "react"
import { Link } from "react-router-dom"
import MusicNotes from "../partials/icons/MusicNotes"
import Search from "../partials/Search"
import Results from "../partials/modals/Results"
import Song from "../partials/Song"
import Footer from "../partials/Footer"
import "../../scss/pages/Home.scss"

import { songs } from "../../songs"

const Home = ({ location }) => {
    
    return (
        <div className={ location.pathname.includes("/search/") ? "home fixed" : "home"}>
            {
                location.pathname.includes("/search/") ?
                <Results /> :
                ""
            }
            <div className="intro">
                <section className={ location.pathname === "/search" ? "hidden" : "" }>
                    <a href="/"><h1>E<span>IMILYRIC</span>S</h1></a>
                    <p>The Wikipedia of Eimi Songs</p>
                    <MusicNotes />
                </section>
                <Search />
                <Link to="/add" className="add-btn">+ Add new song</Link>
            </div>
            <div className="categories">
                <div className="header">
                    <h2>Popular Songs</h2>
                    <nav>
                        <Link to="/gospel" className="nav">Gospel songs</Link>
                        <Link to="/romantic" className="nav">Romantic songs</Link>
                        <Link to="/secular" className="nav">Secular songs</Link>
                    </nav>
                </div>
                <div className="songs">
                    {
                        songs.map(song => {
                            return <Song song={ song } key={ song.id }/>
                        })
                    }
                </div>
            </div>
            <Footer />
        </div>
    )

}

export default Home