const initState = {
    currentForm: 1,
    songExist: null,
    songMayExist: null,
    formOne: {
        activated: true,
        title: "",
        writer: "",
        writers: [],
        dialect: "Paite",
        error: null,
        strict: true
    },
    formTwo: {
        error: null
    }
}

const formData = (state = initState, action) => {
    switch (action.type){
        case "SWITCH_TO_FORM_ONE": {
            return {
                ...state,
                currentForm: 1
            }
        }
        case "EXIT_SONG_EXIST_MODAL":
            return {
                ...state,
                songExist: null
            }
        case "EXIT_SONG_MAY_EXIST_MODAL":
            return {
                ...state,
                songMayExist: null
            }
        case "EMPTY_WRITERS_LIST":
            return {
                ...state,
                formOne: {
                    ...state.formOne,
                    writers: []
                }
            }
        case "UPDATE_WRITERS":
            return {
                ...state,
                formOne: {
                    ...state.formOne,
                    writers: action.writers
                }
            }
        case "FORM_ONE_SUBMITTED":
            return {
                ...state,
                songMayExist: null,
                songExist: null,
                formOne: {
                    ...state.formOne,
                    activated: false,
                    error: null
                }
            }
        case "FORM_ONE_INPUT_CHANGED":
            return {
                ...state,
                formOne: {
                    ...state.formOne,
                    [action.field]: action.value
                }
            }
        case "SONG_NOT_EXIST":
            return {
                ...state,
                currentForm: 2,
                songMayExist: null,
                songExist: null,
                formOne: {
                    ...state.formOne,
                    activated: true,
                    error: null
                }
            }
        case "ENABLE_STRICT_MODE":
            return {
                ...state,
                formOne: {
                    ...state.formOne,
                    strict: true
                }
            }
        case "DISABLE_STRICT_MODE":
            return {
                ...state,
                formOne: {
                    ...state.formOne,
                    strict: false
                }
            }
        case "SONG_MAY_EXIST":
            return {
                ...state,
                songMayExist: action.song,
                songExist: null,
                formOne: {
                    ...state.formOne,
                    activated: true,
                    error: null
                }
            }
        case "SONG_EXIST":
            return {
                ...state,
                songMayExist: null,
                songExist: action.song,
                formOne: {
                    ...state.formOne,
                    activated: true,
                    error: null
                }
            }
        case "FORM_ONE_ERROR":
            return {
                ...state,
                songMayExist: null,
                songExist: null,
                formOne: {
                    ...state.formOne,
                    activated: true,
                    error: action.error
                }
            }
        default:
            return state
    }
}

export default formData