import React, { useState, useEffect } from "react"
import { useSelector, useDispatch } from "react-redux"
import { Link } from "react-router-dom"
import LeftArrow from "../icons/LeftArrow"

const SongExist = () => {

    const dispatch = useDispatch()
    const songExist = useSelector(state => state.formData.songExist)
    const exit = () => {
        dispatch({
            type: "EXIT_SONG_EXIST_MODAL"
        })
    }
    const [ visible, toggleVisibility ] = useState(false)
    useEffect(() => {
        setTimeout(() => toggleVisibility(true), 50)
    }, [])
    
    return (
        <div className="song-exist">
            <div className="bg-layer" onClick={ exit }></div>
            <section className={ visible ? "visible" : "" }>
                <div className="sub-container">
                    <button type="button" className="left-arrow" tabIndex="-1" onClick={ exit }>
                        <LeftArrow />
                    </button>
                    <Link to={ `/song/${ songExist._id }` } className={ songExist.youtube ? "header with-thumbnail" : "header" } style={ songExist.youtube ? { backgroundImage: `url(https://img.youtube.com/vi/${ songExist.youtube }/default.jpg)` } : {} }>
                        <span className="title">{ songExist.title }</span>
                        <span className="message">{ songExist.writer ? `by ` : "" }{ songExist.writer ? <b>{ `${ songExist.writer }, `}</b> : "" }in <b>{ songExist.dialect }</b> already exist in our collection</span>
                    </Link>
                    <div className="lyrics">
                        {
                            songExist.lyrics.split("\\n").map((line, i) => <p key={ i }>{ line }</p>)
                        }
                    </div>
                </div>
                <div className="options">
                    <button type="button" onClick={ exit }>Okay</button>
                    <Link to={ `/edit/${ songExist._id }` }>Make correction</Link>
                </div>
            </section>
        </div>
    )

}

export default SongExist