import React, { useState, useEffect } from "react"
import { withRouter } from "react-router-dom"
import Search from "../../partials/Search"
import Message from "../../partials/Message"
import Song from "../Song"
import "../../../scss/partials/modals/Results.scss"

import { songs } from "../../../songs"

const Results = ({ history }) => {

    const [ visible, updateVisibility ] = useState(false)
    const [ results, updateResults ] = useState([])
    
    useEffect(() => {
        updateVisibility(true)

        setTimeout(() => updateResults(songs), 1000)
    }, [history])

    return (
        <div className="results">
            <div className="bg-layer" onClick={ history.goBack }></div>
            <section className={ visible ? "visible" : "" }>
                <div className="header">
                    <button type="button" className="left-arrow" tabIndex="-1" onClick={ history.goBack }>
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 50 50">
                            <polyline points="25,5 5,25 25,45 5,25 45,25" />
                        </svg>
                    </button>
                    <Search />
                </div>
                {
                    !results.length ?
                    <Message type="loading" message="Searching..." /> :
                    ""
                }
                {
                    results.length > 0 ?
                    <div className="list">
                        <div className="songs">
                            {
                                results.length > 0 ?
                                <p className="results-count">{ results.length } { results.length > 1 ? "results" : "result" } found</p> :
                                ""
                            }
                            {
                                results.map(song => <Song song={ song } key={ song.id } />)
                            }
                        </div>
                    </div> :
                    ""
                }
            </section>
        </div>
    )

}

export default withRouter(Results)